<template>
<!--我想吐槽-->
<div class="spitslot">
	<!--位置定位和二维码展示-->
	<ul class="place">
		<li>
			<span>{{repairPlace}}</span>
			<span class="locat">
				<i><img src="../../assets/splitslot-img/定位.svg"/></i>
				<span class="placeName">{{repairPlace ? buildingMes.name : '请点击右侧扫码定位'}}</span>
			</span>
		</li>
		<li class="scanLocation">
			<scan-code :sourceType="sourceType"></scan-code>
		</li>
	</ul>
	<!--周围环境情况-->
	<ul class="aroundEnvironment" v-if="!!spaceCon.length">
		<li>
			<p class="envirImg">
				<img src="../../assets/splitslot-img/温度icon @1x.png" alt="" />
			</p>
			<p>{{envirArr[1]}}(℃)</p>
			<p><b>{{spaceCon[1]['温度']||'--'}}</b></p>
		</li>
		<li>
			<p class="envirImg">
				<img src="../../assets/splitslot-img/co2icon@1x.png" alt="" />
			</p>
			<p>{{envirArr[0]}}(PPM)</p>
			<p><b>{{spaceCon[0]['CO2']||'--'}}</b></p>
		</li>
		<li>
			<p class="envirImg">
				<img src="../../assets/splitslot-img/sd-icon@1x.png" alt="" />
			</p>
			<p>{{envirArr[2]}}(%)</p>
			<p><b>{{spaceCon[2]['湿度']||'--'}}</b></p>
		</li>
		<li>
			<p class="envirImg">
				<img src="../../assets/splitslot-img/PM2.5-icon@1x.png" alt="" />
			</p>
			<p>{{envirArr[3]}}(PPM)</p>
			<p><b>{{spaceCon[3]['PM2.5']||'--'}}</b></p>
		</li>
	</ul>
	<div class="spitCont">
		<!-- 环境调节 -->
		<div class="spitContWrap clear">
			<ul class="">
				<li class="grouptitle" @click="abc">调节环境</li>
			</ul>
			<ul class="spitContRight" v-if="renderList">
				<li :class="{active:activeIndex==index}" v-for="(item,index) in renderList" @touchstart.prevent="activeClass(item.id,index)" :key="index">
					{{item.name}}
				</li>
			</ul>

		</div>
	</div>
	<div :class="submitclass" @touchstart.prevent="submitSpit">
		提交
	</div>
</div>
</template>
<script type="ecmascript-6">
import {
	Toast,
	MessageBox
} from 'mint-ui'
import scanCode from 'views/repairs/common/scan'
import diaLog from 'views/repairs/my-repairs/diaLog'
import {
	mapActions,
	mapGetters
} from 'vuex'
import tools from 'utils/tools'
// import queryString from 'query-string'
import {
	closeWindow,
	weChatReady,
	getLocation,
	scanQRCode
} from 'api/global'
import {
	getSpitslotArr,
	setSpitslot,
	createSendJob,
	spaceProperty,
	getRepairPlaceArr,
	getSpaceInfo
} from 'api/repair'
export default {
	name: 'spitslot',
	components: {
		'scan-code': scanCode,
	},
	data() {
		return {
			renderList: null,
			activeIndex: -1,
			selectissue: -1,
			objForSubmit: { //用于提交时的信息
				"projectId": 0,
				"spaceId": 0,
				"commentId": ""
			},
			repairPlace: '', //当前空间
			spaceCon: [], // 当前环境下的情况
			spaceParms: '', //	记录扫码后的url
			spaceObj: {
				projectId: '',
				spaceId: ''
			},
			envirArr: [], //周围空间的环境情况排列
			sourceType: 'spitslot',
			reSubmit: false //用于避免重复提交的状态
		}
	},
	computed: {
		...mapGetters([
			'buildingMes',
		]),
		submitclass() {
			if (this.objForSubmit.projectId && this.objForSubmit.spaceId && this.objForSubmit.commentId) {
				return "submit"
			}
			return "cannotsubmit"
		}
	},
	methods: {
		...mapActions(['GetRepairPlaceArr', 'SelectBuildingAndRepair']),
		abc() {
			Toast('已清除缓存')
			tools.rmCookie('openId')
			tools.rmStorage('spaceParms')
		},
		getqueryString(spaceParms) {
			//如果search存在，说明是通过扫描二维码，将拼接的信息保存到url中
			//然后再这里解析获取，projectId,type,spaceId,v
			tools.setStorage('spaceParms', spaceParms);
			const parsedObj = tools.queryString(spaceParms)
			if (!!parsedObj && !!parsedObj.projectId && !!parsedObj.spaceId) {
				this.SelectBuildingAndRepair(parsedObj).then(res => {
					this.buildingMes.name = this.buildingMes.name;
					this.objForSubmit.projectId = this.buildingMes.id;
					this.repairPlace = `${res.leftVal} - ${res.rightVal}`
					this.objForSubmit.spaceId = res.insetId
					spaceProperty(parsedObj).then(res => {
						if (res.data.Result == "success") {
							this.spaceCon = res.data.content;
						}
					}).catch(err => {
						Toast(err)
					});
				}).catch(err => {
					Toast(err)
				})
			}
		},
		activeClass(id, index) {
			//控制点击的选项的样式，对要提交的选项赋值
			this.activeIndex = index
			this.objForSubmit.commentId = id
			this.selectissue = index
			return false
		},
		submitSpit() {
			// 提交吐槽
			if (!this.objForSubmit.projectId) {
				Toast('请选择要吐槽的建筑')
			} else if (!this.objForSubmit.spaceId) {
				Toast('请选择要吐槽的空间')
			} else if (!this.objForSubmit.commentId) {
				Toast('请选择要吐槽的内容')

			} else {
				let isSureToSubmit = false
				switch (this.selectissue) {
					case 0:
						this.SubmitSuccess('感谢您的反馈！即将为您升温1~2℃', '提交成功', '好的');
						break;
					case 1:
						this.SubmitSuccess('感谢您的反馈！即将为您升温3~4℃', '提交成功', '知道了');
						break;
					case 2:
						this.SubmitSuccess('感谢您的反馈！即将为您降温1~2℃', '提交成功', '知道了');
						break;
					case 3:
						this.SubmitSuccess('感谢您的反馈！即将为您降温3~4℃', '提交成功', '知道了');
						break;
					case 4:
						this.SubmitSuccess('感谢您的反馈！即将为您调小风机和风机盘管，若您感觉热请再次调节', '提交成功', '知道了')
						break;
					case 5:
						this.SubmitSuccess('感谢您的反馈！即将为您开启新风设备，可能会有些许噪音', '提交成功', '知道了')
						break;
					case 6:
						let sendInfo = {
							projectId: this.objForSubmit.projectId,
							sites: [{
								id: this.objForSubmit.spaceId,
								objects: []
							}],
							phone: null,
							remark: []
						}
						createSendJob(sendInfo).then(res => {
							if (res.data.Result == 'success') {
								console.log(`工单派遣成功,工单ID:${res  .data.jobId}`)
							}
						}).catch(err => {
							console.log(err)
						})
						this.SubmitSuccess('您的人工服务需求已经提交成功，我们会在20分钟内派一位师傅过去检查，请您耐心等待。', '提交成功', '知道了')
						break;
					case 7:
						this.MessageBoxConfirm('您提交了关闭设备的需求，若选择“关闭”，我们会关闭您所在的空间的设备以节能！', '是否关闭设备？', '关闭')
						break;
				}
			}
		},
		MessageBoxAlert(mes, title, confirmButtonText) {
			//只有确定按钮的
			MessageBox({
					message: mes,
					title: title,
					confirmButtonText: confirmButtonText,
					closeOnClickModal: false,
				})
				.then(action => {
					Toast('提交成功')
					closeWindow()
				}).catch(err => {
					console.log(err)
				})

		},
		MessageBoxConfirm(mes, title) {
			//有确定和取消的
			MessageBox.confirm(mes, title)
				.then(action => {
					this.SubmitSuccess(null, null, null, true);
				}).catch(err => {
					console.log(err)
				})
		},
		SubmitSuccess(mes, title, confirmButtonText, flag) {
			//提交数据到后台
			var regetOpenId = tools.getCookie('openId')
			if (this.reSubmit) {
				return Toast('请勿重复提交')
			}
			this.reSubmit = true;
			if (!!regetOpenId) {
				//在这个页面获取openId，如果openId存在，那么提交 	
				setSpitslot(this.objForSubmit, regetOpenId).then(res => {
					if (res.data.Result == "success") {
						if (!flag) {
							this.MessageBoxAlert(mes, title, confirmButtonText, flag)
						} else {
							Toast('提交成功')
							closeWindow()
						}
					}
				}).catch(err => {
					this.$router.replace({
						name: 'nofound'
					})
				})
			} else {
				//在getters中不存在openId
				this.$router.push({
					name: 'author'
				})
			}
		},
	},
	created() {
		document.title = "调节环境"
	},
	mounted() {
		//   		避免重复提交时的状态
		this.reSubmit = false;
		getSpitslotArr().then(res => {
			if (res.data.Result == "success") {
				//去掉阳光的
				let middleArr = res.data.Content
				middleArr.splice(6, 2)
				this.renderList = res.data.Content
			}
		})
	},
	beforeRouteEnter(to, from, next) {
		var spaceParms = tools.getStorage('spaceParms')
		var winSearch = window.location.search
		var re = /^\?code=/
		if (!spaceParms && !winSearch||re.test(winSearch)) {
			//如果没有缓存，并且没有search，那么会走这里
			weChatReady().then(() => {
				scanQRCode().then(res => {
					//使用cookie存入href    再将href用querySting传入
					let v = +new Date()
					let locationHref = `spitslot?projectId=${res.projectId}&type=${res.type}&spaceId=${res.spaceId}&v=${v}`
					const Parms = locationHref.split('?')[1];
					tools.setStorage('spaceParms', '?' + Parms);
					window.location.replace(locationHref)
				}).catch(err => {
					//获取权限失败
					Toast('取消扫码，即将退出到公众号界面')
				})
			}).catch((err) => {
				console.error('ready' + JSON.stringify(err))
			})
			setTimeout(() => {
				next()
			}, 0);
		} else if (!!winSearch) {
				next(vm => {
					vm.getqueryString.call(vm, winSearch)
				});
			//如果没缓存，但是有search，说明是扫码进入的
		} else {
			//如果没有search，但是有缓存，走缓存了
			next(vm => {
				vm.getqueryString.call(vm, spaceParms)
			});
		}
	}
}
</script>
<style lang="less">
/*.spitslot .scan{
	position: fixed;
	top:0;
	left:0;
}*/

@rem: 30rem;
.spitContRight {
	li.active {
		color: #fff;
		background-color: #44b197;
	}
}

.spitContRight {
	li {
		float: left;
		width: 50%;
		height: 112/@rem;
		line-height: 112/@rem;
		background: white;
		font-size: 30/@rem;
		color: #333333;
		border-top: 2/@rem solid #f0f0f0;
		border-right: 2/@rem solid #f0f0f0;
		text-align: center;
		font-family: "pingfangSC-MEDIUM";
	}
	li:nth-child(2n) {
		border-right: none;
	}
	li:nth-child(7),
	li:nth-child(8) {
		border-right: none;
		width: 100%;
	}
}

.spitCont {
	padding: 20/@rem 16/@rem;
	padding-bottom: 180/@rem;
}

.grouptitle {
	background: #fbfbfb;
	height: 80/@rem;
	line-height: 84/@rem;
	padding-left: 24/@rem;
	color: #6d6d6d;
	font-size: 28/@rem;
	font-family: "pingfangSC-regular";
}

.locat {
	display: block;
	padding: 20/@rem 0 0 0;
	background: white;
	.placeName {
		font-size: 28/@rem;
		color: #6d6d6d;
		font-family: "pingfangSC-regular";
	}
	i {
		display: inline-block;
		width: 27/@rem;
		height: 36/@rem;
		img {
			vertical-align: middle;
			height: 100%;
		}
	}
}

.place {
	/*padding-top:30/@rem;*/
	display: flex;
	height: 164/@rem;
	padding: 0 48/@rem;
	/*padding: 30/@rem;*/
	background: white;
	li {
		align-self: center;
	}
	li:first-child {
		flex: 1;
		span:first-child {
			color: #333333;
			font-family: "pingfangSC-MEDIUM";
			font-weight: bold;
			font-size: 36/@rem;
		}
	}
	.scanLocation {
		width: 110/@rem;
		height: 89/@rem;
		font-weight: lighter;
		font-size: 20/@rem;
		color: #929292;
		/*overflow: hidden;*/
	}
}

.aroundEnvironment {
	display: flex;
	text-align: center;
	/*background: white;*/
	justify-content: space-around;
	/*height: 205/@rem;*/
	/*padding-top: 33/@rem;*/
	li:first-child {
		margin-left: 0;
	}
	li {
		flex: 1;
		text-align: center;
		font-size: 15/@rem;
		margin: 2/@rem 0 0 2/@rem;
		/*height: 200/@rem;*/
		background: white;
		padding: 34/@rem 0 34/@rem 0;
		p:first-child {
			display: inline-block;
			width: 35/@rem;
			height: 56/@rem;
			img {
				/*height: 100%;*/
				width: 100%;
			}
		}
		P {
			text-align: center;
		}
		p:nth-child(2) {
			color: #6D6D6D;
			font-size: 20/@rem;
			font-family: "pingfangSC-regular";
			padding: 20/@rem 0;
		}
		p:last-child {
			b {
				color: #333333;
				font-size: 30/@rem;
				font-family: "pingfangSC-MEDIUM";
				font-weight: bold;
			}
		}
	}
	li:first-child {
		p:first-child {
			width: 28/@rem;
		}
	}
}

.mint-msgbox {
	top: 45%;
	border-radius: 18/@rem;
	/*width:531/@rem;*/
	.mint-msgbox-header {
		padding: 0;
	}
	.mint-msgbox-title {
		color: #000000;
		font-size: 36/@rem;
		font-family: "pingfangSC-regular";
		padding: 50/@rem 0 20/@rem 0;
	}
	.mint-msgbox-content {
		padding: 0;
	}
	.mint-msgbox-message {
		color: #88888;
		font-size: 30/@rem;
		font-family: "pingfangSC-regular";
		line-height: 42/@rem;
		padding: 0 55/@rem 40/@rem;
		text-align: center;
	}
	.mint-msgbox-btns {
		height: 100/@rem;
		font-size: 36/@rem;
		font-family: "pingfangSC-MEDIUM";
	}
	.mint-msgbox-confirm {
		color: #44b197;
		font-size: 36/@rem;
	}
	.mint-msgbox-cancel {
		color: #333333;
		font-size: 36/@rem;
	}
}
</style>
<style scoped lang="less">
/*转换rem*/

@rem: 30rem;
.mint-msgbox {
	border-radius: 18/@rem;
}

.submit {
	width: 100%;
	height: 100/@rem;
	position: fixed;
	bottom: 0;
	left: 0;
	text-align: center;
	line-height: 100/@rem;
	color: #fff;
	background-color: #44b197;
}

.cannotsubmit {
	width: 100%;
	height: 100/@rem;
	position: fixed;
	bottom: 0;
	left: 0;
	text-align: center;
	line-height: 100/@rem;
	color: #fff;
	background-color: #8e8f99;
}

.point {
	color: #44b197;
	font-size: 35/@rem;
	display: inline-block;
	padding-top: 12/@rem;
	width: 40/@rem;
	text-align: right;
	margin-right: 9/@rem;
	margin-left: 6/@rem;
}
</style>


